@import "normalizes.scss";
@import "resets.scss";

* {
    box-sizing: border-box; }

$color-stroke: #ffec81;
$color-radius: #44f3be;

.lakki-reddy-regular {
  font-family: "Lakki Reddy", serif;
  font-weight: regular;
  font-style: normal; }


.container {
    max-width: 1140px;
    margin: 0 auto; }

.main-section {
    position: relative;
    width: 1024px;
    height: 1024px;
    min-height: 100%;
    min-width: 100%;
    background: rgba(238, 183, 121, 0.96);
    padding-top: 16px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px; } }
.ava {
    margin: 0px; }

.li {
    position: relative;
    top: -80px;
    left: 650px;
    margin: left 50px;
    margin-right: 70px; }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 70px;
        font-size: 32px;
        color: white;
        text-decoration: none;
        border: 2px solid $color-radius;
        border-bottom: 5px solid #44f3be;
        border-radius: 50px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: white; } } }
a:hover {
    color: rgb(167, 80, 0); }

